import React, { Component } from "react";
import GridList from "../components/GridList";
import { getHome } from "../request/request.js";
import Loading from "../components/Loading";
import Tab from "./Tab";

class Home extends Component {
	state = {
		newReleases: [],
		featured: [],
		isLoading: false,
		featuredTotal: 0
	};

	async componentDidMount() {
		document.title = "aniba - Watch free anime without ads!";
		await this.fecthNewReleases();
		await this.fetchFeatured();
	}

	fecthNewReleases = async () => {
		this.setState({ isLoading: true });

		let homeData = sessionStorage.getItem("home");
		let dateExp = sessionStorage.getItem("homeExp");
		if ((Date.now() - dateExp) / 1000 <= 360 && homeData) {
			homeData = JSON.parse(homeData);
		} else {
			let { data, total } = await getHome("type=new_releases");
			homeData = data;
			this.setState({ featuredTotal: total });
			sessionStorage.setItem("homeExp", Date.now());
		}

		this.setState({ newReleases: [...homeData], isLoading: false });
	};

	fetchFeatured = async () => {
		let featuredData = JSON.parse(sessionStorage.getItem(`featured`));
		let featuredExp = sessionStorage.getItem(`featuredExp`);
		let featuredTotal = sessionStorage.getItem(`featuredTotal`);
		featuredTotal = Math.max(
			this.state.featuredTotal,
			Number(featuredTotal), 4
		);
		this.setState({ isLoading: true })
		if (
			(Date.now() - featuredExp) / 240000 <= 1 &&
			featuredData &&
			featuredData.length === Number(featuredTotal)
		) {
			this.setState({ featured: featuredData, isLoading: false });
		} else {
			for (let i = 0; i < featuredTotal; i++) {
				this.setState({ isLoading: true });
				let { data } = await getHome(`type=browse&limit=1&skip=${i}`);
				this.setState({
					featured: [...this.state.featured, ...data],
					isLoading: false
				});
				sessionStorage.setItem("featuredExp", Date.now());
			}
		}
		this.setState({ featuredTotal });
	};

	componentWillUnmount() {
		sessionStorage.setItem("featuredTotal", this.state.featuredTotal);

		if (this.state.newReleases.length > 0) {
			sessionStorage.setItem(
				"home",
				JSON.stringify(this.state.newReleases)
			);
		}

		if (this.state.featured.length > 0) {
			sessionStorage.setItem(
				"featured",
				JSON.stringify(this.state.featured)
			);
		}
	}

	render() {
		return (
			<div>
				<div className="mb-40 pt-sm-20 mb-sm-70 page-fade-in">
					<div className="grid-scroll-blocker" />
					{this.state.newReleases.map(item => (
						<GridList
							data={item.content}
							title={item.title}
							key={item._id}
						/>
					))}
					{this.state.featured.map(item => (
						<GridList
							data={item.content}
							title={item.title}
							key={item._id}
						/>
					))}
					{this.state.isLoading && <Loading />}
				</div>
				<Tab active="Home"></Tab>
			</div>
		);
	}
}

export default Home;
